import store from "src/store";
import {updateTesting} from "src/actions/testingActions";

export const functionRun = (name)=>{
    let allCounts = store.getState().testing?.counts ? store.getState().testing.counts : {};
    let count = allCounts.hasOwnProperty(name) ? allCounts[name] : 0;
    allCounts[name]=count+1;
    store.dispatch(updateTesting({counts:allCounts}));
};
