import React from 'react';
import ModalCloseButton from "../ModalCloseButton";
import {functionRun} from "src/containers/testing/testingFunctions";


const clicker = (args) => {
    functionRun("close");
}

const ModalCloseButtonTest = ()=>{
    return (
        <div>
            <div></div>
            <ModalCloseButton dismisser="yoyoyo" click={clicker} />
        </div>
    )
}

export default ModalCloseButtonTest;
