import Button from "react-bootstrap/Button";
import React from "react";
import {closeModal} from "src/utilities/utilityFunctions";

const ModalCloseButton = (props) => {
    let clickFunc = props.click ? props.click : closeModal;
    return (
        <Button variant="secondary" onClick={() =>{clickFunc(props.dismisser)}} id="modalCloseButton">
            Close
        </Button>
    )
};

export default ModalCloseButton;
